main {
	margin: 0 auto;
	max-width: 90%;
  padding: 50px 30px;
}
:root {
  --primary: darkred;
  --secondary: goldenrod;
  --light: antiquewhite;
  --dark: #0D3B66;
  --darker: #000000;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
}

body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: .1em;
  background: var(--light);
  color: var(--dark);
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
  text-decoration: underline;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--secondary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 00px;
  background-color:var(--primary);
  color:var(--secondary);
 }
 
 header nav {
  margin: 5px 0;
 }
 
 header nav h1 {
  padding: 0 10px;
 }
 
 header nav ul {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  list-style: none;
 }
    
 header nav ul li {
  padding: 20px;
  font-size: 25px;
 }

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

footer {
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-style:italic;
  background: var(--dark);
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

li {
  list-style: none;
}

nav  li {
  cursor: pointer;
}

textarea {
	width: 600px;
	height: 120px;
	border: 3px solid #cccccc;
	padding: 5px;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

button {
	background: var(--primary);
	border: 0;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
	font-size: 16px;
	font-weight: 600;
	margin-bottom: 30px;
	padding: 10px 15px;	
}

a {
	border-radius: 5px;
	color: var(--primary);
	font-weight: 600;
	padding: 2px 4px;
	margin-left: -2px;
	margin-right: -2px;
}

.navActive {
  color: var(--secondary);
}

.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.home-link {
  font-size: 42px;
}

.about-me {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.bio {
  text-align: center;
}

.bio-pic {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-right: 75px;
}

.section-title {
  color: var(--secondary-color);
  text-align:center;
  font-size: 50px;
  border-bottom: 3px solid;
  padding-bottom: 10px;
  margin: 0 auto 35px auto;
  width: 50%;
 }

 .projects-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  padding-top: 25px;
  padding-bottom: 25px;
 }

 .card {
  width: 300px;
  color: var(--dark);
 }

 .media-content {
  text-align: center;
 }

 .card-content a {
  color: var(--secondary)
 }

 .footer {
  background: var(--primary);
  color: var(--secondary);
 }

 .footer a {
  color: var(--secondary);
 }

 .proficiencies {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around
 }

 .ftr-link {
  padding: 5px;
  width: 64px;
 }

 #resume {
  text-align: center;
 }

 #resume a {
  color: var(--secondary);
  text-decoration: underline;
 }

 #contact-form {
  text-align: center;
  padding-left: 300px;
 }